import './App.css';
import axios from 'axios';
import { useEffect,useState } from 'react';
import { Container, Switch, withStyles } from '@material-ui/core';
import Header from './components/Header/Header';
import Definitions from './components/Definitions/Definitions';
import { grey } from '@material-ui/core/colors';
function App() {
  const [word, setword] = useState("")
  //console.log(word);
  const [meanings, setmeanings] = useState([]);
  const [category, setcategory] = useState("en")
  const [lightMode, setlightMode] = useState(false);

  const ThemeSwitch = withStyles({
    switchBase: {
      color: grey[300],
      '&$checked': {
        color: grey[500],
      },
      '&$checked + $track': {
        backgroundColor: grey[500],
      },
    },
    checked: {},
    track: {},
  })(Switch);

  const  dictionaryApi = async () =>{
    try{
      const data = await axios.get(`https://api.dictionaryapi.dev/api/v2/entries/${category}/${word}`);
      //console.log(data);
      // setmeanings(data.data[0].meanings[0].definitions[0].definition);
      setmeanings(data.data);
    }catch(error){
      console.log(error);
    }
  }

  useEffect(()=>{
    dictionaryApi();
  },[word,category]);
 
 // console.log(meanings);
  return (
    <div className="App" style={{height:'100vh',backgroundColor:lightMode?'#fff':'#282c34',color:lightMode ? 'black':  'white',
    transition:"all 0.5s linear"}}>
      {/* the container component makes the website responsive */}
      <Container maxWidth="md" style={{display:"flex",flexDirection:"column",height:'100vh',justifyContent:"space-evenly"}}>
        
        <div style={{position:"absolute",top:0,right:15,paddingTop:10}}>
        <span>{lightMode?"Dark":"Light"} Mode</span>
          <ThemeSwitch checked={lightMode} onChange={()=>setlightMode(!lightMode)}></ThemeSwitch>
        </div>

        <Header category={category} setcategory={setcategory} word={word} setword = {setword} lightMode={lightMode}></Header>

        {meanings && (<Definitions word ={word} meanings={meanings} category={category} lightMode={lightMode}/>)}

      </Container>
    </div>
  );
}

export default App;
